<template>
  <div class="w-100" style="border-bottom-left-radius: .3rem; border-bottom-right-radius: .3rem;">
    <h1 class="text-left m-3"><strong>Recommendation</strong></h1>
    <hr/>
    <div v-if="isLogin">
      <!-- <span v-if="profile.articles.length"> -->
        <div class="mx-3">
          <div v-if="!recommendScore"> !</div>
          <div v-else>
            <br><h4>{{searchedMovie[0].title}}   <span id="dif-txt">{{ recommendScore.toFixed(2) }}</span>! </h4>
            <br>
            <div v-if="recommendScore >= 4">   <h4 class="font-italic d-inline">" " </h4> .</div>
            <div v-else-if="recommendScore >= 3">   <h4 class="font-italic d-inline">" " </h4> .</div>
            <div v-else-if="recommendScore >= 2">   <h4 class="font-italic d-inline">" " </h4> .</div>
            <div v-else>   <h4 class="font-italic d-inline">"" </h4> .</div>
            <br><br>
            <div id="barBottom" />
            <div id="barTop" :style="{ width: vwidth }"/>
            <br>
          </div>
        </div>
      <!-- </span> -->
      <!-- <span v-else>
        <div class="my-5 mx-1">
          <h4 class="text-center my-3">     !</h4>
          <b-button size="lg" @click="goCommunity" variant="info"> </b-button>
        </div>
      </span> -->
    </div>

    <div v-else class="my-5 mx-1">
      <h4 class="text-center my-3">      !</h4>
      <b-button size="lg" @click="goLogin" variant="secondary"></b-button>
    </div>


  </div>
</template>

<script>
export default {
  name: "Recommend",
  methods: {
    goLogin: function () {
      this.$router.push({ name: "Login" });
    },
    goCommunity: function () {
      this.$router.push({ name: "Community" });
    },
  },
  computed: {
    isLogin: function () {
      return this.$store.state.is_login;
    },
    profile: function () {
      return this.$store.state.profile
    },
    searchedMovie: function () {
      return this.$store.state.searchedMovie
    },
    recommendScore: function () {
      return this.$store.state.recommendScore
    },
    vwidth: function () {
      return `${this.recommendScore*20}%`
    }
  },
};
</script>

<style>
#dif-txt {
  color: lightcoral;
}
#barTop {
  background-color: violet;
  height: 3vh;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  position: relative;
  top: -3vh;
}
#barBottom {
  background-color: midnightblue;
  height: 3vh;
  width: 100%;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  position: relative;
}
</style>