<template>
  <div>
    <section id="analytics">
        <h1 id="analytics-title" class="text-light my-5">
          <strong>{{ username }} M<span id="small-txt">(movie)</span>BTI</strong>
        </h1>

      <!--  -->
      <section v-if="profile.articles.length" id="recommend-movie">
        <!--MBTI -->
        <section>
          <br><h2>{{ this.myMBTI }}</h2><br>
        </section>
        <!--  ( ,  )-->
        <section>
          <MyReviews @rateAvg="showResult"/><br>
        </section>
        <!-- -->
        <section class="my-3">
          <CloudChart/><br>
        </section>
        <!-- -->
        <section class="my-3">
          <MyMovie/>
        </section>
      </section> 

      <!--   -->
      <section v-else>
        <div class="my-5 mx-1" id="recommend-movie">
          <h4 class="text-center my-3">      !</h4>
          <b-button size="lg" class="mb-3"  @click="goCommunity" variant="secondary"></b-button>
        </div>
      </section>  
    </section>
    <br /><br /><br />
  </div>
</template>

<script>
import MyReviews from '@/components/Analytics/MyReviews'
import MyMovie from '@/components/Analytics/MyMovie'
import CloudChart from '@/components/Analytics/CloudChart'
// import axios from "axios"
// const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
  name: "Analytics",
  components: {
    MyReviews,
    MyMovie,
    CloudChart,
  },
  data: function () {
    return {
      myMBTI: '',
    }
  },
  methods: {
    goCommunity: function () {
      this.$router.push({ name: 'Community' })
    },
    showResult: function (avg) {
      if (avg === 0 || avg === 1) {
        this.myMBTI = '      !'
      }
      else if (avg === 2 || avg === 3) {
        this.myMBTI = '     !       '
      }
      else {
        this.myMBTI = '     ANGEL     !'
      }
      
    }
  },
  computed: {
    username: function () {
      return this.$store.state.username
    },
    profile: function () {
      return this.$store.state.profile
    }
  },
  created: function () {
    const isLogin = this.$store.state.is_login

    if (isLogin === false) {
      this.$router.push({ name: "Login" })
    }
    this.$store.dispatch('getReviews')
    this.$store.dispatch('getProfile', this.username)

    window.scrollTo(0, 0)
  },
};
</script>

<style>
#analytics {
  width: 60%; /*  80%   */
  margin: 0 auto; /*     */
  padding: 1rem 0;
  color: black;
}
#analytics-title {
  font-size: 5rem;
}
#recommend-movie {
  border-top-left-radius: .3rem;
  border-top-right-radius: .3rem;
  border-bottom-left-radius: .3rem;
  border-bottom-right-radius: .3rem;
  padding: 1rem 1rem;
  background-color: rgb(0, 0, 0, 0.6);
  color: #ede8f1;
}
#recent-review {
  width: 70%;
  margin-top: 1rem;
  margin-right: 1rem;
  border-right: 3pt groove lightgrey;
}
#small-txt {
  font-size: 2rem;
  color: lightcoral;
}
</style>